.about-us {
  padding: 120px 0;

  &__title {
    margin-bottom: 24px;

    font-size: 32px;
    line-height: 48px;
    text-align: center;
    color: $c_dark-blue;

    @extend %font-poppins-semi-bold;

    @include screen-sm {
      margin-bottom: 16px;
    }
  }

  &__text {
    font-size: 22px;
    line-height: 33px;
    text-align: center;
    color: $c_medium-grey;

    @extend %font-open-sans-semi-bold;

    @include screen-md {
      margin: 0 auto;

      max-width: 716px;
      font-size: 24px;
      line-height: 36px;
    }

    @include screen-lg {
      max-width: 754px;
    }
  }

  @include screen-sm {
    padding: 148px 0;
  }
}
